    overflow: hidden;
    border: 1px solid #ccc;
    background-color: #f1f1f1;
  }
  
  /* Style the buttons inside the tab */
 .rah-card .tab button {
    background-color: inherit;
    float: left;
    border: none;
    outline: none;
    cursor: pointer;
    padding: 14px 16px;
    transition: 0.3s;
    font-size: 17px;
  }

  .rah-card .MuiPaper-root{
    /* position: relative; */
    margin-top: 0 !important;
  }
  
  /* Change background color of buttons on hover */
 .rah-card .tab button:hover {
    background-color: #ddd;
  }
  
  /* Create an active/current tablink class */
 .rah-card .tab button.active {
    background-color: #ccc;
  }
  
  /* Style the tab content */
 .rah-card .tabcontent {
    display: none;
    padding: 6px 12px;
    border: 1px solid #ccc;
    border-top: none;
  }

