.main-footer ul li a::after{
    content: "";
    position: absolute;
    bottom: -3px;
    left: 0;
    width: 0;
    height: 1px;
    background-color: #fff;
    transition: all 0.3s ease-in-out;
}

.main-footer a:hover {
    color: #fff;
    transition: all 0.3s ease-in-out;
    text-decoration: none;
}

.main-footer a:hover::after{
    width: 100%;
    transition: all 0.3s ease-in-out;

